.welcome {
    box-sizing: border-box;
    margin: 0 0 2px 0;
    width: calc(100vw - 20vw);
    height: calc(100vh - 80px);
    overflow: hidden;
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    color: white;
    margin: 0 auto 50px;
    font-family: 'Open Sans', sans-serif;
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
}
.welcomeBox {
    /* box-sizing: border-box;
    border: solid 1px; */
    margin: 0 auto;
    background-color: rgba(4, 11, 12, 0.5);
    border-radius: 20px;
}
.welcomeBoxLine1 {
    /* box-sizing: border-box;
    border: solid 1px; */
    margin: auto 1px;
    font-size: 85px;
}
.welcomeBoxLine2 {
    /* box-sizing: border-box;
    border: solid 1px; */
    margin: 1px auto;
    font-size: 70px;
}
.welcomeBoxLine2Span1 {
    background-color: white;
    color: black;
    font-weight: bold;
}
.welcomeBoxLine3 {
    /* box-sizing: border-box;
    border: solid 1px; */
    margin: 1px auto;
    font-size: 85px;
}
.welcomeBoxLine4 {
    /* box-sizing: border-box;
    border: solid 1px; */
    margin: 1px auto;
    font-size: 102px;
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .welcome {
        width: calc(100vw - 10vw);
        margin: 0 auto 50px;
    }
    .welcomeBoxLine1 {
        font-size: 65px;
    }
    .welcomeBoxLine2 {
        font-size: 48px;
    }
    .welcomeBoxLine3 {
        font-size: 59px;
    }
    .welcomeBoxLine4 {
        font-size: 70px;
    }
}