.about {
    box-sizing: border-box;
    /* border: solid 1px; */
    color: white;
    width: calc(100vw - 20vw);
    margin: 0 auto 50px;
    font-family: 'Open Sans', sans-serif;
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
    padding-top: 25px;
}
.aboutTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 2.5px 5px;
    font-size: 30px;
    font-weight: 700;
    margin: 15px 0;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 2.5px;
    color: black;
    width: fit-content;
}
.aboutContent {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 2.5px 5px;
    font-size: 20px;
    margin-bottom: 15px;
    background-color: rgba(4, 11, 12, 0.95);
}
.aboutTable {
    display: flex;
    flex-direction: column;
    background-color: rgba(4, 11, 12, 0.5);
    margin-bottom: 20px;
}
.aboutTableLineImg {
    display: flex;
    flex-direction: row;
    margin-bottom: 5px;
}
.aboutTableLineImgFirstCell {
    box-sizing: border-box;
    /* border: solid 1px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
    width: calc(80vw * 0.3);
}
.aboutTableLineImgCell {
    box-sizing: border-box;
    /* border: solid 1px; */
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: calc((80vw * 0.7)/4);
}
.aboutTableLineImgCell img {
    width: calc((77vw * 0.7)/4);
    height: 10vw;
    border-radius: 5px;
}
.aboutTableLineSubTitle {
    display: flex;
    flex-direction: row;
}
.aboutTableLineSubTitleFirstCell {
    box-sizing: border-box;
    border-bottom: solid 1px rgba(255, 255, 255, 0.5);
    text-align: center;
    width: calc(80vw * 0.3);
    color: rgb(4, 11, 12);
    background-color: white;
    font-weight: bold;
    margin: 2px 0;
    padding: 5px 0;
}
.aboutTableLineSubTitleCell {
    box-sizing: border-box;
    border-left: solid 2px rgba(0, 0, 0, 0.5);
    text-align: center;
    width: calc((80vw * 0.7)/4);
    color: rgb(4, 11, 12);
    background-color: white;
    font-weight: bold;
    margin: 2px 0;
    padding: 5px 0;
}
.aboutTableLineBlank {
    display: flex;
    flex-direction: row;
    background-color: rgba(4, 11, 12, 0.7);
}
.aboutTableLineBlankFirstCell {
    box-sizing: border-box;
    border-bottom: solid 1px rgba(255, 255, 255, 0.7);
    text-align: center;
    width: calc(80vw * 0.3);
    padding: 5px 0;
}
.aboutTableLineBlankCell {
    box-sizing: border-box;
    border-left: solid 0.5px rgba(255, 255, 255, 0.1);
    border-bottom: solid 1px rgba(255, 255, 255, 0.7);
    text-align: center;
    width: calc((80vw * 0.7)/4);
    font-size: 25px;
    font-weight: bold;
    padding: 5px 0;
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .about {
        width: calc(100vw - 10vw);
        margin: 0 auto 50px;
    }
    .aboutTitle {
        font-size: 25px;
    }
    .aboutTableLineImgFirstCell {
        width: calc(90vw * 0.25);
    }
    .aboutTableLineImgCell {
        width: calc((90vw * 0.75)/4);
    }
    .aboutTableLineImgCell img {
        width: calc((87vw * 0.7)/4);
    }
    .aboutTableLineSubTitleFirstCell {
        width: calc(90vw * 0.25);
        font-size: 11px;
    }
    .aboutTableLineSubTitleCell {
        width: calc((90vw * 0.75)/4);
        font-size: 10px;
    }
    .aboutTableLineBlankFirstCell {
        width: calc(90vw * 0.25);
        font-size: 12px;
    }
    .aboutTableLineBlankCell {
        width: calc((90vw * 0.75)/4);
        font-size: 20px;
    }
}