.portifolio {
    box-sizing: border-box;
    /* border: solid 1px; */
    color: white;
    width: calc(100vw - 20vw);
    margin: 0 auto 50px;
    font-family: 'Open Sans', sans-serif;
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
    padding-top: 25px;
}
.portifolioTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 2.5px 5px;
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 2.5px;
    color: black;
    width: fit-content;
}
.project {
    box-sizing: border-box;
    /* border: solid 1px; */
    margin: 10px auto 25px;
}
.projectBoard {
    display: flex;
    flex-direction: row;
    margin-bottom: 50px;
    background-color: rgba(4, 11, 12, 0.95);
}
.projectBoardDescription {
    width: calc(80vw * 0.4);
    margin: 2px auto;
    display: flex;
    flex-direction: column;
}
.projectBoardDescriptionTitle {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 5px 10px;
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
}
.projectBoardDescriptionText {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 5px 10px;
    font-size: 20px;
    margin-bottom: 10px;
}
.projectBoardDescriptionLink {
    box-sizing: border-box;
    /* border: solid 1px; */
    padding: 10px;
    font-size: 20px;
    font-weight: bold;
    margin: 0;
    margin-top: auto;
}
.projectBoardDescriptionLink a {
    text-decoration: none;
    background-color: rgb(46, 191, 255);
    color: white;
    padding: 5px;
    border-radius: 5px;
}
.projectBoardImg {
    box-sizing: border-box;
    border: solid 1px rgba(255, 255, 255, 0.25);
    border-radius: 5px 5px 0 0;
    width: calc(80vw * 0.6);
    margin: 2px auto;
    display: flex;
    flex-direction: column;
}
.projectBoardImgMain {
    width: calc(80vw * 0.6);
    height: 300px;
    display: flex;
    flex-direction: row;
}
.projectBoardImgMainClick {
    width: auto;
    max-width: calc(80vw * 0.6);
    height: 295px;
    margin: 1px auto;
}
.projectBoardImgThumbnail {
    display: flex;
    flex-direction: row;
    height: auto;
    overflow-x: scroll;
}
.projectBoardImgThumbnailCell {
    width:  calc((80vw * 0.6 )/ 5);
    height: 100px;
    margin: 2px;
}
.projectBoardImgThumbnailCellClick {
    width:  calc((80vw * 0.55 )/ 5);
    height: 95px;
    border: solid 1px rgb(46, 191, 255);
    cursor: pointer;
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .portifolio {
        width: calc(100vw - 10vw);
        margin: 0 auto 50px;
    }
    .portifolioTitle {
        font-size: 25px;
    }
    .projectBoard {
        flex-direction: column;
    }
    .projectBoardDescription {
        width: 90vw;
    }
    .projectBoardDescriptionTitle {
        font-size: 25px;
    }
    .projectBoardDescriptionLink {
        margin-bottom: 10px;
    }
    .projectBoardImg {
        width: 90vw;
    }
    .projectBoardImgMain {
        height: 250px;
    }
    .projectBoardImgMainClick {
        max-height: 245px;
        max-width:  90vw;
        margin: 0 auto;
    }
    .projectBoardImgThumbnailCell {
        width:  calc(80vw / 5);
        height: 60px;
    }
    .projectBoardImgThumbnailCellClick {
        width:  calc(79.5vw / 5);
        height: 55px;
    }
}