.ui {
    border-bottom: solid 1px rgba(255, 255, 255, 0.25);
    box-sizing: border-box;
    position: sticky;
    top: 0;
    left: 0;
    width: calc(100vw - 20vw);
    height: 80px;
    background-color: rgb(4, 11, 12);
    color: white;
    z-index: 90;
    padding: 15px 0 5px;
    margin: 0 auto;
}
.uiHeader {
    display: flex;
    flex-direction: row;
    box-sizing: border-box;
}
.uiHeaderLogo {
    /* border: solid 1px; */
    box-sizing: border-box;
    margin: 0 15px 0 0px;
    cursor: default;
}
.uiHeaderLogoTitle {
    /* border: solid 1px; */
    box-sizing: border-box;
    font-size: 30px;
    padding: 1px;
    text-align: center;
    font-family: 'Black Ops One', cursive;
}
.uiHeaderLogoDescription {
    /* border: solid 1px; */
    box-sizing: border-box;
    font-size: 10px;
    padding: 1px;
    text-align: center;
    font-family: 'Open Sans', sans-serif;
}
.uiHeaderMenu {
    /* border: solid 1px; */
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    font-family: 'Open Sans', sans-serif;
}
.uiHeaderMenuButton {
    /* border: solid 1px; */
    box-sizing: border-box;
    border-radius: 8px;
    margin: auto 10px;
    text-align: center;
    padding: 0 1px;
    font-size: 20px;
    font-weight: bold;
    cursor: pointer;
}
.uiHeaderMenuButton:hover {
    background-color: white;
    color: black;
}
.uiBurger {
    /* border: solid 1px; */
    box-sizing: border-box;
    right: 15px;
    z-index: 95;
}
.uiBurgerIcon {
    /* border: solid 1px;  */
    box-sizing: border-box;
    position: sticky;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    z-index: 95;
    cursor: pointer;
    padding: 10px 0;
    margin-left: calc(90vw - 175px);
}
.uiBurgerIconBar {
    box-sizing: border-box;
    background-color: white; 
    width: 25px;
    height: 3px;
    margin: 3px auto;
}
.uiBurgerMenu {
    /* border: solid 1px; */
    box-sizing: border-box;
    position: fixed;
    z-index: 90;
    top: 61px;
    right: -130px;
    padding: 2px;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    will-change: transition;
    transition: linear 300ms;
    background-color: white;
    color: black;
    border-radius: 10px 0 0 10px;
}
.uiBurgerMenuButton {
    box-sizing: border-box;
    border-bottom: solid 1px rgba(128, 128, 128, 0.5);
    text-align: center;
    padding: 2px;
    text-align: left;
    width: 120px;
    cursor: pointer;
    text-align: center;
}
.uiBurgerMenuButton:hover {
    color: white;
    background-color: black;
    border-radius: 10px;
}
.uiBurgerBackground {
    position: fixed;
    top: 0;
    right: -200vw;
    width: 200vw;
    height: 200vh;
    background-color: rgba(0, 0, 0, 0.75);
    z-index: 70;
    will-change: transition;
    transition: linear 300ms;

}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {
    .uiBurger {
        display: none;
    }
}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .ui {
        width: calc(100vw - 10vw);
        margin: 0 auto;
    }
    .uiHeaderMenu {
        display: none;
    }
}