.contact {
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    /* border: solid 1px; */
    color: white;
    width: calc(100vw - 20vw);
    min-height: calc(100vh - 80px);
    margin: 0 auto;
    font-family: 'Open Sans', sans-serif;
    font-size: 20px;
    padding-top: 25px;
}
.contactBoard {
    display: flex;
    flex-direction: column;
    width: 80vw;
    font-size: 20px;
    margin: 2px auto;
    /* margin-bottom: auto; */
}
.contactBoardTitle {
    padding: 2.5px 5px;
    font-size: 30px;
    font-weight: 700;
    margin-bottom: 10px;
    background-color: rgba(255, 255, 255, 0.9);
    border-radius: 2.5px;
    color: black;
    width: fit-content;
}
.contactBoardLine {
    padding: 5px;
    background-color: rgba(4, 11, 12, 0.85);
}
.contactBoardLineSpan {
    font-size: 15px;
    color: thistle;
}
.contactBoardImg {
    box-sizing: border-box;
    border: solid 1px;
    max-width: 50vw;
    margin: 50px auto 10px;
}
.contactBoardImg img {
    max-width: 50vw;
}
.contactBoardMsg {
    text-align: center;
    margin: 10px auto;
}
/* Configuração da visualização desktop */
@media(min-width: 610.5px) {

}
/* Configuração da visualização mobile */
@media(max-width: 610px) {
    .contact {
        width: 90vw;
    }
    .contactBoard {
        width: 90vw;
    }
    .contactBoardTitle {
        font-size: 25px;
    }

}